import React from "react";

export const NavItem = ({
    flexGrow,
    size,
    customSize,
    linkText,
    textColor,
    key
}) => {
    const styles = {
        flexGrow: flexGrow,
        padding:    size == 'xs'? '10px' : 
                    size == 'md' ? '15px':
                    size == 'lg' ? '20px' : 
                    customSize == undefined ? '10px' : customSize,
        color: textColor != undefined ? textColor : undefined 
        
    }

    return(
        <>
            <a style={styles}>{linkText}</a>
        </>
    )
}