import React from "react";

/*
- The number of navigation links
- the orientation of the links (vertical, horizontal)
- slide through the links or scroll in the event of too many links not being able to
  fit in the navbar
*/

export const Navbar = ({
    orientation, 
    isScroll, 
    isSlider, 
    horizontalAlignment,
    verticalAlignment,
    color,
    children 
}) =>{ 


    const styles = { 
        backgroundColor: color == undefined ? '#3291a8' : color ,
        display: 'flex',
        flexDirection: orientation == 'row' ? 'row' : 'column',
        justifyContent: horizontalAlignment == "center" ? 'center' :
                        "start",
        alignContent: verticalAlignment == "center" ? 'center' :
                        "start",               
    }

    
    return(
        <>
            <nav style={styles}>
                {children}
            </nav> 
        </>
    )
}